*,
*::before,
*::after {
  box-sizing: inherit; }

html {
  box-sizing: border-box; }

.is-hidden {
  display: none !important; }

@media (max-width: 1024px) {
  .for-desktop {
    display: none !important; } }

@media (min-width: 1025px) {
  .for-mobile {
    display: none !important; } }

.clearfix {
  overflow: auto; }
  .clearfix::after {
    content: "";
    display: table;
    clear: both; }

body {
  background-color: #fff;
  font-family: "MegaFonGraphikLC", sans-serif;
  font-weight: normal;
  font-stretch: normal;
  font-style: normal;
  letter-spacing: normal;
  font-size: 15px;
  line-height: 1.6;
  color: #333;
  text-align: left; }

.container {
  max-width: 1280px;
  width: 100%;
  margin: 0 auto; }
  @media (max-width: 1024px) {
    .container {
      max-width: 1024px; } }
  @media (max-width: 768px) {
    .container {
      max-width: 768px; } }
  @media (max-width: 500px) {
    .container {
      max-width: 500px; } }

.background--grey {
  background-color: #ededed;
  width: 100%;
  height: 197px;
  display: flex;
  align-items: center;
  justify-content: flex-start; }
  @media (max-width: 1024px) {
    .background--grey {
      height: 160px; } }
  @media (max-width: 768px) {
    .background--grey {
      height: 152px; } }
  @media (max-width: 500px) {
    .background--grey {
      height: 148px; } }
  .background--grey--footer {
    height: 40px; }

.section__title {
  font-size: 52px;
  font-weight: bold;
  line-height: 1.08;
  letter-spacing: 0.5px;
  color: #333;
  box-sizing: border-box;
  padding-left: 80px; }
  @media (max-width: 1024px) {
    .section__title {
      font-size: 44px;
      line-height: 1.09;
      letter-spacing: normal; } }
  @media (max-width: 768px) {
    .section__title {
      font-size: 36px;
      line-height: 1.11; } }
  @media (max-width: 500px) {
    .section__title {
      font-size: 30px;
      line-height: 1.2;
      padding-left: 32px; } }

.section__info {
  padding: 42px 146px 100px;
  box-sizing: border-box;
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-wrap: wrap; }
  @media (max-width: 1024px) {
    .section__info {
      padding: 20px 65px 75px; } }
  @media (max-width: 768px) {
    .section__info {
      padding: 20px 50px 75px; } }
  @media (max-width: 500px) {
    .section__info {
      flex-direction: column;
      padding-left: 32px;
      padding-right: 32px; } }

.info__item {
  width: 32%;
  max-width: 300px;
  margin-top: 58px; }
  @media (max-width: 1024px) {
    .info__item {
      height: auto;
      margin-top: 0; } }
  @media (max-width: 500px) {
    .info__item {
      width: 100%; } }
  @media (max-width: 768px) {
    .info__item--text {
      display: flex;
      width: 100%;
      max-width: 768px;
      align-items: flex-start;
      margin-top: 40px; } }
  @media (max-width: 500px) {
    .info__item--text {
      flex-direction: column;
      align-items: center;
      text-align: center; } }
  .info__item-icon {
    width: 32px;
    height: 32px;
    display: inline-block;
    vertical-align: middle;
    margin-right: 10px;
    transition: .35s; }
    @media (min-width: 769px) {
      .info__item-icon:hover {
        transform: rotate(90deg) scale(1.7); } }
    @media (max-width: 1024px) {
      .info__item-icon {
        display: block;
        margin: 55px auto 0; } }
    @media (max-width: 500px) {
      .info__item-icon {
        margin-top: 36px; } }
    .info__item-icon--text {
      margin-left: 4px;
      display: block; }
      @media (max-width: 768px) {
        .info__item-icon--text {
          margin-top: 0;
          margin-right: 20px; } }
      @media (max-width: 500px) {
        .info__item-icon--text {
          margin: auto auto 16px; } }
  .info__item-string {
    display: inline-block;
    vertical-align: middle; }
    @media (max-width: 1024px) {
      .info__item-string {
        display: block;
        margin-top: 16px;
        text-align: center; } }
    .info__item-string--bolder {
      font-weight: 500; }
    .info__item-string--text {
      display: block;
      margin-top: 16px; }
      @media (max-width: 1024px) {
        .info__item-string--text {
          text-align: left; } }
      @media (max-width: 768px) {
        .info__item-string--text {
          margin-top: 0; } }
      @media (max-width: 500px) {
        .info__item-string--text {
          text-align: center; } }
  .info__item-text {
    margin-top: 16px; }
    .info__item-text--under {
      margin-top: 4px; }
    @media (max-width: 768px) {
      .info__item-text {
        margin-top: 0; } }
